
'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star, PartyPopper, AlertTriangle } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePageBackground';
import { Badge } from '@/components/ui/badge';
import { useRouter, useSearchParams } from 'next/navigation';
import { Skeleton } from '@/components/ui/skeleton';
import { Alert, AlertTitle, AlertDescription } from '@/components/ui/alert';
import { useToast } from '@/hooks/use-toast';

const RazorpaySubscriptionButton = ({ userId, customerId }: { userId: string; customerId: string }) => {
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!userId || !customerId) {
      setError("User or Customer ID is missing.");
      setIsLoading(false);
      return;
    }

    const loadRazorpayScript = () => {
      try {
        const rzpForm = document.getElementById('razorpay-form');
        if (!rzpForm) {
          throw new Error("Razorpay form container not found.");
        }
        while (rzpForm.firstChild) {
          rzpForm.removeChild(rzpForm.firstChild);
        }
        
        const script = document.createElement('script');
        script.src = 'https://cdn.razorpay.com/static/widget/subscription-button.js';
        script.async = true;
        script.setAttribute('data-subscription_button_id', 'pl_Ohp287a2N1d5gR');
        script.setAttribute('data-button_theme', 'brand-color');
        script.setAttribute('data-customer_id', customerId);
        
        script.onload = () => setIsLoading(false);
        script.onerror = () => {
          setError("Failed to load payment script. Please check your network connection.");
          setIsLoading(false);
        };
        
        rzpForm.appendChild(script);

      } catch (e: any) {
        setError(e.message || "An unexpected error occurred.");
        setIsLoading(false);
      }
    };

    const timer = setTimeout(loadRazorpayScript, 100);
    return () => clearTimeout(timer);
  }, [userId, customerId]);

  if (error) {
    return (
      <Alert variant="destructive">
        <AlertTriangle className="h-4 w-4" />
        <AlertTitle>Payment Error</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  if (isLoading) {
    return <Skeleton className="h-12 w-full max-w-xs" />;
  }

  return <form id="razorpay-form" className="w-full flex justify-center"></form>;
};

function PricingContent() {
  usePageBackground('');
  const { user, profile, isLoading, upgradeToPro, createRazorpayCustomer } = useAuth();
  const router = useRouter();
  const searchParams = useSearchParams();
  const { toast } = useToast();
  const [isUpgrading, setIsUpgrading] = useState(false);
  const [isCustomerReady, setIsCustomerReady] = useState(false);

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [isLoading, user, router]);

  useEffect(() => {
    if (user && profile) {
      if (profile.razorpayCustomerId) {
        setIsCustomerReady(true);
      } else {
        createRazorpayCustomer().then(() => {
          setIsCustomerReady(true);
        }).catch(err => {
          toast({
            title: "Initialization Error",
            description: "Could not initialize payment details. Please refresh and try again.",
            variant: "destructive",
          });
        });
      }
    }
  }, [user, profile, createRazorpayCustomer, toast]);

  useEffect(() => {
    const subscriptionId = searchParams.get('razorpay_subscription_id');
    if (subscriptionId && user && profile?.subscriptionStatus !== 'pro' && !isUpgrading) {
      setIsUpgrading(true);
      
      toast({
        title: 'Payment Successful!',
        description: 'Verifying and upgrading your account to Pro...',
      });

      const checkUpgradeStatus = async (attempt = 1) => {
        const maxAttempts = 6;
        try {
          await upgradeToPro(subscriptionId);
          toast({
            title: 'Welcome to Pro!',
            description: 'Your account has been successfully upgraded.',
          });
          
          const url = new URL(window.location.href);
          url.searchParams.delete('razorpay_subscription_id');
          router.replace(url.pathname, { scroll: false });
          setIsUpgrading(false);
          
        } catch (error) {
          if (attempt < maxAttempts) {
            setTimeout(() => checkUpgradeStatus(attempt + 1), 5000);
          } else {
            toast({
              title: 'Upgrade Processing',
              description: 'Your payment was successful. If your account is not upgraded in a few minutes, please contact support.',
              variant: 'default',
            });
            setIsUpgrading(false);
          }
        }
      };

      setTimeout(() => checkUpgradeStatus(), 3000);
    }
  }, [searchParams, user, profile, upgradeToPro, toast, router, isUpgrading]);

  if (isLoading || !user || !profile) {
    return (
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-12">
          <Skeleton className="h-12 w-3/4 mx-auto" />
          <Skeleton className="h-6 w-1/2 mx-auto mt-4" />
        </div>
        <div className="flex justify-center px-4 sm:px-0">
          <Skeleton className="h-[500px] w-full max-w-md" />
        </div>
      </div>
    );
  }

  const isAlreadyPro = profile.subscriptionStatus === 'pro';

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-foreground font-headline sm:text-5xl">
          {isAlreadyPro ? "Welcome to the Pro Club!" : "Unlock Your Full Potential"}
        </h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-muted-foreground">
          {isAlreadyPro ? "You have access to all features. Keep up the great work!" : "Choose the plan that's right for you and start mastering mental math today."}
        </p>
      </div>
      
      <div className="flex justify-center px-4 sm:px-0">
        <Card className="w-full max-w-md shadow-2xl border-2 border-primary md:scale-105">
          <CardHeader className="text-center bg-primary/10 p-6">
            {isAlreadyPro ? <PartyPopper className="w-12 h-12 text-primary mx-auto mb-4" /> : <Star className="w-12 h-12 text-primary mx-auto mb-4" />}
            <CardTitle className="text-3xl font-bold">Abacus Pro</CardTitle>
            <CardDescription className="text-lg">
              {isAlreadyPro ? "Your access to premium features is active." : "Everything you need to become a math whiz."}
            </CardDescription>
            {!isAlreadyPro && (
              <div className="mt-4 flex flex-col items-center gap-2">
                <div className="flex items-baseline gap-4">
                  <p className="text-2xl font-semibold text-muted-foreground line-through">₹799</p>
                  <Badge variant="destructive" className="text-base h-fit py-1 px-3 -rotate-12 transform">55% OFF</Badge>
                </div>
                <p className="text-5xl font-extrabold tracking-tight text-foreground">₹360<span className="text-base font-medium text-muted-foreground">/month</span></p>
              </div>
            )}
          </CardHeader>
          <CardContent className="p-6 md:p-8">
            <ul className="space-y-4">
              {['Unlimited Practice Tests', 'All Difficulty Levels', 'Detailed Progress Tracking', 'No Advertisements', 'Priority Support'].map((feature) => (
                <li key={feature} className="flex items-center gap-3">
                  <Check className="h-6 w-6 text-green-500" />
                  <span className="text-muted-foreground">{feature}</span>
                </li>
              ))}
            </ul>
          </CardContent>
          <CardFooter>
            {isAlreadyPro ? (
              <div className="w-full text-center text-primary font-semibold text-lg flex items-center justify-center gap-2">
                <Check className="w-6 h-6"/> You are a Pro member!
              </div>
            ) : isUpgrading ? (
              <div className="w-full text-center text-blue-600 font-semibold text-lg flex items-center justify-center gap-2">
                <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
                Processing upgrade...
              </div>
            ) : (
              <div className="w-full flex justify-center">
                {isCustomerReady && profile.razorpayCustomerId ? (
                  <RazorpaySubscriptionButton userId={user.uid} customerId={profile.razorpayCustomerId} />
                ) : (
                  <div className="text-center">
                    <Skeleton className="h-12 w-full max-w-xs mb-2" />
                    <p className="text-sm text-muted-foreground">Initializing payment...</p>
                  </div>
                )}
              </div>
            )}
          </CardFooter>
        </Card>
      </div>
    </div>
  );
}

export default function PricingPage() {
    return <PricingContent />;
}

